<template>
	<div class="create">
		<!-- <mt-header title="编辑家人" class="createName">
			<router-link to="/focusFamily" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
		</mt-header> -->
		<div class="focus">
			<mt-field label="称呼" placeholder="请输入称呼" v-model="family.appellation"></mt-field>
		</div>
		<div class="next" style="margin-top:20px;">
			<mt-button class="next-btn" @click.native="submit">确定</mt-button>
		</div>
		<div class="next" style="margin-top:20px;">
			<mt-button class="next-btn delete-btn" @click.native="deleteClick">删除</mt-button>
		</div>
	</div>
</template>
<script>
	export default {
		data (){
			return {
				family: {
					appellation: this.$route.params.appellation,
					focu_id:this.$route.params.focu_id
				}
			}
		},
		methods:{
			submit () {
				if (this.family.appellation&&this.family.appellation!='') {

						//提交被编辑关注人接口
						var resource = this.$resource(this.$store.state.serverIP+'/user_focus/edit_focu?'+location.search.substring(1).split("&")[0]);
						resource.save(this.family).then((response) => {
		        			this.$toast({
		        				message: '提交成功',
		        				position: 'bottom'
		        			});
		        			setTimeout(() => {
		        				this.$router.push('/focusFamily');
		        			}, 1000);
						}, (response) => {
		     				this.$toast(response.data.error);
						})

				}else{
					this.$toast({
						message: '请输入对方称呼',
						position: 'bottom'
					});
				}
			},
			deleteClick (){
				//删除关注
				var resource = this.$resource(this.$store.state.serverIP+'/user_focus/remove_focu?'+location.search.substring(1).split("&")[0]+'&focu_id='+this.$route.params.focu_id);
				resource.get().then((response) => {
					this.$toast({
						message: '删除成功',
						position: 'bottom'
					});
					setTimeout(() =>{
						this.$router.push('/focusFamily');
					},1000);
				},(response) =>{
					this.$toast(response.data.error);
				})
			}
		}
	}
</script>
<style>
.next .delete-btn{
	background-color: #fff;
	border: 1px solid #000;
	color: #323232;
}
</style>



